/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --text:					#252525;
  --dividers:				#e6e6e6;
  --footer:				linear-gradient(#e4e4e4, #cdcdcd) no-repeat border-box;
  --header-border:		#b5b4b5;
  --main:					rgba(0,0,0,.15);
  --overlay:				rgba(255,255,255,.15);
  --tabs:					#f0f0f0;
  --tab-dividers:			#cecece;
  --table-text:			#7b7b7b;
  --table-border:			#c1c1c1;
  --stripe-odd:			#f4f5f5;
  --stripe-even:			#fff;
  --sorted-odd:			#f4f5f5;
  --sorted-even:			#fff;
  --select:				#e0e0e0;
  --select-sort:			#e0e0e0;
  --details-table:		rgba(255,255,255,.5);
  --details-divider:		rgba(0,0,0,.15);
  --side-text:			rgba(0,0,0,.85);
  --side-hover:			rgba(0,0,0,.15);
  --modal-header:			rgba(0,0,0,.15);
  --menu:					#ededed;
  --input-color:			#000000;
  --input-border:			#cccccc;
  --input-background:		#ffffff;
  --select:				#ffffff;
  --select-border:		#b2b2b2;
  --button:				#ffffff;
  --button-color:			rgba(0,0,0,.85);
  --button-border:		#c0c0c0;
}

[data-theme^="dark"] {
  --text:					rgba(255,255,255,1);
  --dividers:				rgba(0,0,0,.85);
  --footer:				linear-gradient(rgba(86,86,86,.5), rgba(58,58,58,.5)) no-repeat border-box;
  --header-border:		rgba(0,0,0,.85);
  --main:					rgba(0,0,0,.15);
  --overlay:				rgba(0,0,0,.15);
  --tabs:					rgba(80,80,80,.4);
  --tab-dividers:			rgba(164,164,164,.4);
  --table-text:			rgba(255,255,255,.65);
  --table-border:			rgba(0,0,0,.85);
  --stripe-odd:			rgba(60,60,60,.4);
  --stripe-even:			rgba(34,34,34,.4);
  --sorted-odd:			transparent;
  --sorted-even:			transparent;
  --select:				rgba(80,80,80,.5);
  --select-sort:			rgba(80,80,80,.5);
  --details-table:		rgba(35,35,35,.85);
  --details-divider:		rgba(255,255,255,.15);
  --side-text:			rgba(255,255,255,1);
  --side-hover:			rgba(255,255,255,.15);
  --modal-header:			rgba(255,255,255,.1);
  --menu: 				rgba(54,54,54,.85);
  --input-color:			rgba(255,255,255,.75);
  --input-border:			rgba(255,255,255,.1);
  --input-background:		rgba(255,255,255,.05);
  --select:				rgba(0,0,0,.5);
  --select-border:		rgba(164,164,164,.4);
  --button:				rgba(255,255,255,.25);
  --button-color:			rgba(255,255,255,.85);
  --button-border:		rgba(0,0,0,.35);
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  user-select: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: var(--text);
}

::placeholder {
  opacity: .6;
  transition: opacity .2s;
}

:focus::placeholder {
  opacity: 0;
}

input,
textarea {
  background: transparent;
}

button.ui-button {
  padding: 3px 14px;
  color: var(--button-color);
  background: var(--button);
  border: 1px solid var(--button-border);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
  background-clip: border-box;
  font-size: 13px;
  border-radius: 5px;
}

button.ui-button.small {
  font-size: 11px;
  border-radius: 3px;
  padding: 2px 12px;
}

button.ui-button:focus {
  color: #fff;
  background: var(--accent);
  background-image: linear-gradient(var(--accent-light), var(--accent));
  border: 1px solid var(--accent);
  outline: none;
}

button.ui-button:active {
  color: #fff;
  background: var(--accent-dark);
  background-image: linear-gradient(var(--accent), var(--accent-dark));
  border: 1px solid var(--accent-dark);
}

button.ui-button:disabled {
  opacity: .5;
}

[data-theme^="dark"] ::placeholder {
  color: #fff;
}

body {
  text-align: center;
  padding: 0;
}

body header {
  height: 70px;
  padding-top: 10px;
}

body header figure img {
  -webkit-user-drag: none;
  -webkit-app-region: no-drag;
}

body main {
  padding: 80px 20px 0;
  height: 270px;
}

body main h1 {
  font-size: 15px;
  margin-bottom: 20px;
  color: var(--text);
  font-weight: 500;
}

body main p {
  font-size: 13px;
  width: 160px;
  margin: 0 auto 15px;
  color: var(--text);
}

body main dl {
  font-size: 11px;
  line-height: 14px;
  margin-bottom: 15px;
}

body main dl dt, body main dl dd {
  display: inline;
  color: var(--text);
}

body main dl dt {
  padding-right: 5px;
}

body main dl dd::after {
  display: block;
  content: '';
  white-space: pre-line;
}
